.container-wrapper {
  padding: 20px 0px;
}

.text-ell {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.img-block{
  width: 20% !important;
  display: flex;
  align-self: center;
  padding-top: 7px;
}

.title {
  font-size: 18px;
  font-weight: 900;
}

.padding-12 {
  padding: 12px;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
